.detailWrapper {
  margin: 6rem 0;
  display: flex;
  justify-content: space-between;
}

.info {
  margin-right: 5rem;
}

.info p {
  font-size: 20px;
  line-height: 34px;
  color: #383838;
  font-weight: 500;
  text-decoration: none;
}

.info b {
  font-weight: 500;
}

.info a {
  font-size: 20px;
  line-height: 26px;
  text-decoration-color: #50c56a;
}

h2 {
  position: absolute;
  z-index: 10;
  padding: 10px 15px;
  background: #e9f477;
  border-radius: 0 10px 10px 0;
  font-weight: 500;
  top: 340px;
}

ul {
  margin-top: 2rem;
}

ul span {
  color: #50c56a;
}

ul li {
  font-size: 1.2rem;
  line-height: 2.5rem;
  color: #383838;
  font-weight: 500;
  list-style: none;
}

.buttons {
  margin-bottom: 30px;
}

.button {
  padding: 1rem 2rem;
  color: #313131;
  background: #fff;
  border: 1px solid #50c56a;
  margin-right: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.button.active {
  background: #50c56a;
  color: #fff;
}

.infoImage {
  position: relative;
}

@media (max-width: 1420px) {
  .info {
    width: 100%;
  }

  .buttons button {
    display: block;
    margin-bottom: 10px;
    width: 100%;
  }
}

@media (max-width: 1220px) {
  .detailWrapper {
    display: grid;
  }

  .info {
    order: 1;
    margin-right: 0;
  }

  .infoImage {
    margin-bottom: 50px;
  }

  h2 {
    bottom: -15px;
    top: auto;
  }

  .buttons {
    display: flex;
  }
}

@media (max-width: 760px) {
  .infoImage img {
    width: 100%;
  }

  .buttons {
    display: block;
  }

  h2 {
    font-size: 1.2rem;
  }
}

@media (max-width: 520px) {
  body {
    margin: 0 5%;
  }

  h2 {
    padding: 8px 12px;
    font-size: 1rem;
  }

  .info p {
    font-size: 18px;
    line-height: 28px;
  }
}

@media (max-width: 400px) {
  h2 {
    font-size: 0.8rem;
  }
  .info p {
    font-size: 16px;
    line-height: 26px;
  }
}
