.list {
  display: flex;
  justify-content: center;
  margin: 2rem 0rem;
}

.link,
.activeLink {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
  margin-right: 2rem;
  text-decoration: none;
  background: #50c56a;
  width: 10rem;
  height: 5rem;
  cursor: pointer;
  transform: scale(0.8);
}

.activeLink {
  background: linear-gradient(to right, #3bba29, #d7e656);
  color: #fff;
}

.link svg {
  color: #fff;
  font-size: 1.5rem;
}

 h4 {
  color: #fff;
  font-size: 16px;
  margin-top: 0.5rem;
}

@media (max-width: 860px) {
  .list {
    display: block;
  }
  .link {
    max-width: 400px;
    width: 100%;
    margin: 0 auto;
  }
  .activeLink {
    color: #fff;
    margin: 0 auto;
    max-width: 400px;
    width: 100%;
  }
}

@media (max-width: 460px) {
  .activeLink,
  .link {
    height: 4rem;
  }
}
